import { Progress } from "antd"
import "../styles/result_page.css"

import { useState, useEffect } from "react"

import AppLayout from "../components/layout"

function ResultPage(props){

    const [score,setScore] = useState(60)

    const getUserMark=()=>{
        const mark = localStorage.getItem("mark")
        setScore(mark)
    }

    useEffect(()=>{
        getUserMark()
    },[]) // to run only once and that is when our page load

    // useEffect(()=>{
    //     console.log("score has changed");
    // },[score])

    return(
            <AppLayout>
                <div id="result-page-container">
                    <h1>Your Result</h1>
                    <Progress type="circle" percent={score} strokeColor={score > 50 ? "green" : "red"} />
                    {
                        score <= 50 ?
                            <h3>You performed poorly</h3>
                        :
                            <h3>You Passed the test</h3>
                    }
                    {/* <p onClick={()=>{
                        setScore(score+1)
                    }}>increment score</p> */}
                </div>
            </AppLayout>
    )
}


export default ResultPage