import Question from "../components/qestion"

import { Button } from 'antd';

import {questions} from "../data/questions"

import { useState } from "react";

import { Link,useNavigate } from "react-router-dom";

import AppLayout from "../components/layout";


function FirstPage(props){

    const navigate = useNavigate()

    const [marks,setMark] = useState(0)

    // marks = 0
    // incrementMark()
    // incrementMark()

    function incrementMark(){
        const current_mark = marks + 1
        setMark(current_mark)

        console.log("current mark is : ", current_mark);

        // current_mark = 2
        // setMark(current_mark)    setMark(2)
    }

    

    //this function will run when we click on our submt button
    function getMarks(){
        const mark_per_question = 100/questions.length 

        const overall_mark = marks * mark_per_question
        // return user mark
        console.log("overall mark is ", overall_mark, "%");

        //store marks inside localStorage
        localStorage.setItem("mark",overall_mark)

        //redirect to result page
        navigate("/result")
        
    }

    return (
        <AppLayout>
            {
                questions.map((current,index)=>(
                <Question 
                    incrementMark={incrementMark}
                    text={current.question}
                    options={current.options}
                    correct_option_pos = {current.correctAnswer}
                    key={"question_"+index}
                />
                ))
            }
            <div id="btn-container">
                <Button type="primary" size={"large"} onClick={getMarks}>
                    Submit
                </Button>
                <p>Hello</p>
                <Link to={"result"}>Go to Result page</Link> <br/>
                <Link to={"about-us"}>About Us Page</Link>
            </div>
        </AppLayout>
    )
}

export default FirstPage