import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import { Menu } from 'antd';


import "../styles/menu.css"

function getItem(label, key, icon, children, type) {
  return {
    key,
    icon,
    children,
    label,
    type,
  };
}

const items = [
    getItem("Today's task", 'grp', null, [
        getItem('All Tasks', '1'),
        getItem('Important Tasks', '2'),
        getItem('Tasks done', '3'),
        getItem('Uncompleted Tasks', '4')],
        'group'),
        getItem('Directories', 'sub4', null, [
            getItem('Main', '5'),
          ]),
  ];

function MenuSlider(props){
    return(
        <div>
            <div id="main-heading">TO-DO TASK</div>
            
            <div id="add-task">Add new task</div>

            <div id="menu">
            <Menu
                onClick={null}
                style={{
                    width: 256,
                }}
                defaultSelectedKeys={['1']}
                defaultOpenKeys={['sub1']}
                mode="inline"
                items={items}
                />
            </div>

        </div>
    )
}

export default MenuSlider